import React,{Component} from "react";
import "./index.scss";

class ListThree extends Component{
	constructor(props) {
		super(props);
		this.state = {
			nature:['1','2','3','4','5','6']
		}
		
	}


	render() {
		return (

			<div id="listone">
				<div className="listone-top">
					<p></p>
					<div className="send"></div>
				</div>
				<div className="listone-center">
					<p>()</p>
					<ul className="nature">
						{
							this.state.nature.map((item,index)=>
								<li className="nature-li" key={new Date().getTime()+index}>
									<span className="nature-char"></span>
									<span className="nature-brand"></span>
								</li>
							)
						}
					</ul>
				</div>
				<div className="listone-bottom">
					<a href="#" className="empty"></a>
					<a href="#" className="ensure"></a>
				</div>
			</div>
		)
	}
}

export default ListThree;


