import React,{Component} from "react";
import "./index.scss";

class Login extends Component{
	constructor(props) {
		super(props);
		
	}


	render() {
		return (

			<div id="xy_login">
				<header>
					<span onClick={this.bindleClick.bind(this)}></span>
				</header>
				<div id="input">
					<input type="text" className="tel" placeholder=""/>
					<input type="text" className="vC" placeholder=""/>
					<span></span>
				</div>
				<p><a href="javascript:;"></a></p>
				<button></button>
				<a href="javascript:;" className="aboutUs"></a>
				{this.props.children}
			</div>

		)
	}
	bindleClick(){
		this.props.history.push(`/code`);
	}
}

export default Login;


